<template>
    <div class="appbox">
        <div class="popup">
            <p class="tip">请截图或者使用文本，妥善备份您的账号密码，账户一旦丢失，账户内的资产永久不可找回。</p>
            <div class="main">
                <p class="top">您的账号</p>
                <p class="bottom" id="counterText">{{accountInfo.username}}</p>
                <button class="copy" @click="copy(accountInfo.username)">复制账号</button>
            </div>
            <div class="main pwd">
                <p class="top">您的密码</p>
                <p class="bottom" id="psdText">{{accountInfo.password}}</p>
                <button class="copy" @click="copy(accountInfo.password)">复制密码</button>
            </div>
        </div>
    </div>
</template>

<script>
    import { accountInfo } from '@/api/user'
    import Clipboard from 'clipboard'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    export default {
        name: "Backups",
        data()
        {
            return {

                accountInfo:{
                    username:'',
                    password:''
                }
            }
        },
        mounted()
        {
            this.getAccountInfo()
        },
        methods:{
            // 获取邀请信息
            getAccountInfo()
            {
                accountInfo().then(res => {
                    this.accountInfo = res
                })
            },
            // 复制邀请码
            copy(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    Toast.success('复制成功');
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    Toast.fail('当前浏览器不支持');
                    // 释放内存
                    clipboard.destroy()
                })

            }
        }
    }
</script>

<style scoped>
     @import "../../static/css/backups.css";
</style>



